body {
  background: #66D3FA;
  background-image: url("https://images.unsplash.com/photo-1587059481645-b3a17becd6e0?q=80&w=1965&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow: hidden;
}

.navbar {
  background-color: lightblue;
  padding: 10px;
  position: absolute; /* Position navbar absolutely */
  top: 0; /* Align to top of the page */
  left: 0; /* Align to left of the page */
  width: 100%; /* Extend navbar width to the right edge */
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-start; /* Align items to the left */
}

.navbar ul li {
  margin-right: 20px;
  cursor: pointer;
  position: relative; /* Add position relative */
}

.navbar ul li::after {
  content: "";
  position: absolute;
  width: 0;
  height: 3px; /* Increased height */
  background-color: blue;
  bottom: -4px; /* Adjusted bottom position */
  left: 0;
  border-radius: 4px; /* Added border radius */
  transition: width 0.3s ease; /* Add transition */
}

.navbar ul li:hover::after {
  width: 100%; /* Change width to 100% on hover */
}

.App {
  display: flex;
  flex-direction: column; /* Set flex direction to column */
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.content {
  text-align: center;
}

.home-icon {
  width: 20px; /* Adjust width as needed */
  height: auto; /* Maintain aspect ratio */
  margin-right: 5px;
}
