
.registration-container {
  border: 2px solid #007bff; /* Add black border */
  border-radius: 10px; /* Add border radius */
  padding: 20px; /* Add padding for better appearance */
  background-color: #66D3FA;
  /* opacity: 0.5; */
}
.registration-container {
width: 300px;
margin: 150px auto;
}

h1 {
text-align: center;
}

.form-group {
margin-bottom: 15px;
}

label {
display: block;
margin-bottom: 5px;
margin-right:300px;
}

input[type="text"],
input[type="password"] {
width: 100%;
padding: 8px;
border: 1px solid #ccc;
border-radius: 4px;
}

button {
width: 100%;
padding: 10px;
background-color: #007bff;
color: #fff;
border: none;
border-radius: 4px;
cursor: pointer;
}

button:hover {
background-color: #0056b3;
}

#successMessage {
position: fixed;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
padding: 10px;
background-color: #d4edda;
border: 1px solid #c3e6cb;
border-radius: 4px;
color: #155724;
z-index: 9999;
}
